import React, {useEffect, useState} from 'react';

import {Panel, PanelHeader} from '@vkontakte/vkui/';
import './Home.css'

import fire from '../audio/fire.mp3'
import fire_img from '../img/fire_img.png'


const Home = ({ id }) => {
	const [volume, setVolume] = useState(1)
	const [audio, setAudio] = useState(fire)
	const [play, setPlay] = useState(false)
	const [thisAudio, setThisAudio] = useState(new Audio())

	useEffect(() => {
		thisAudio.src = audio
		thisAudio.loop = true;
		thisAudio.volume = volume
	}, [audio] )

	useEffect(() => {

		if (play) {
			thisAudio.play()
		} else {
			thisAudio.pause()
			console.log('pause')
		}
		
	}, [play])

	return (
	<Panel id={id}>
		<PanelHeader>Подборка</PanelHeader>
		<div className='audio-fles'>
			<img className='audio-fle' src={fire_img} alt='fire' onClick={() => {
				setAudio(fire)
				setPlay(!play)
			}}/>
			<img className='audio-fle' src={fire_img} alt='fire' onClick={() => {
				setAudio(fire)
				setPlay(!play)
			}}/>
			<img className='audio-fle' src={fire_img} alt='fire' onClick={() => {
				setAudio(fire)
				setPlay(!play)
			}}/>
			<img className='audio-fle' src={fire_img} alt='fire' onClick={() => {
				setAudio(fire)
				setPlay(!play)
			}}/>
			<img className='audio-fle' src={fire_img} alt='fire' onClick={() => {
				setAudio(fire)
				setPlay(!play)
			}}/>
			<img className='audio-fle' src={fire_img} alt='fire' onClick={() => {
				setAudio(fire)
				setPlay(!play)
			}}/>
		</div>
	</Panel>)
}
        
export default Home;
